<template>
  <router-link :to="{name: 'projectDetail', params: {slug: project.slug}}" class="overflow-hidden bg-gray-100 dark:bg-gray-900 cursor-pointer rounded-lg relative group h-64 md:h-96 border-2 border-gray-900">
    <div class="rounded-xl z-10 lg:opacity-0 group-hover:opacity-100 transition duration-300 ease-in-out cursor-pointer absolute from-black/80 to-transparent bg-gradient-to-t inset-x-0 -bottom-2 pt-30 dark:text-white flex items-end">
        <div class="dark:text-white text-black w-full">
          <div class="transform p-4 text-xl group-hover:opacity-100 group-hover:translate-y-0 translate-y-4 pb-10 transition duration-300 ease-in-out bg-gray-500 w-full bg-opacity-20">
            <h2 class="font-bold text-gray-800 dark:text-black">{{project.title}}</h2>
            <small class="text-gray-600 dark:text-gray-900 text-lg mb-3">Date Finished: {{project.date}}</small>
            <div class="text-sm dark:text-black line-clamp-1">
                {{project.description}}
            </div>
            <div class="flex flex-wrap">
              <template v-for="(tag, index) in project.tag" :key="index">
                <span @click.stop.prevent="$router.push({name: 'tag', query: { q: tag }})" :class="{'bg-gray-300 dark:bg-gray-500': tag.toLowerCase() == $route.query.q.toLowerCase(), 'bg-gray-200 dark:bg-gray-600': tag.toLowerCase() != $route.query.q.toLowerCase() }" class="font-normal m-1 hover:bg-gray-300 rounded-full px-2 text-sm leading-loose cursor-pointer transition-colors duration-300 dark:hover:bg-gray-500">
                  {{tag}}
                </span>
              </template>
            </div>
          </div>
        </div>
    </div>
    <img alt="gambar project firmanmimang" class="object-scale-down w-full h-full group-hover:scale-110 transition duration-300 ease-in-out" v-lazy="getImgUrl(project.image[0])"/>
  </router-link>
</template>
<script>
export default {
  props: ['project'],
  methods:{
    getImgUrl(pic) {
      return require('../assets/project-foto/'+pic)
    }
  },
}
</script>