<template>
  <div class="p-6">
    <h1 data-aos="fade-in" data-aos-duration="1000" class="text-center mb-10 font-semibold text-4xl lg:px-14 dark:text-white">Projects {{$route.query.q}}</h1>
    <div class="grid md:grid-cols-2 lg:grid-cols-3 lg:px-14 gap-5">
      <template v-if="projectsFilter.length > 0">
        <Card data-aos="fade-up" data-aos-duration="1000" :data-aos-delay="200*(index%3)" v-for="(project, index) in projectsFilter" :key="index" :project="project"/>
      </template>
      <template v-else>
        <p data-aos="fade-in" data-aos-duration="1000" class="text-2xl font-semibold text-center col-span-full text-red-500">Projects that have {{$route.query.q}} tag can't be found.</p>
      </template>
    </div>
  </div>
</template>

<script>
import Card from '@/components/CardTag'
import { useMeta } from 'vue-meta'
import { useRoute } from 'vue-router'

export default {
  components:{
    Card
  },
  setup () {
    useMeta({ title: `Tag ${useRoute().query.q}` })
  },
  computed:{
    projectsFilter(){
      return this.$store.getters.filterByTagProject(this.$route.query.q)
    }
  },
}
</script>